                <el-form-item label="合同负责人：">
                    <el-select v-model="formEdit.origin" placeholder="默认自己">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="所属部门：">
                    <el-select v-model="formEdit.origin" placeholder="请选择">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="合同状态：">
                    <el-select v-model="formEdit.origin" placeholder="请选择">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="客户签约人：">
                    <el-input v-model="formEdit.mail"></el-input>
                </el-form-item>
                <el-form-item label="我方签约人：">
                    <el-input v-model="formEdit.mail"></el-input>
                </el-form-item>
                <el-form-item label="签约日期：">
                    <el-date-picker
                        v-model="formEdit.value1"
                        type="date"
                        placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="备注：">
                    <el-input type="textarea" v-model="formEdit.notes" :rows="4"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogEditVisible = false" size="small">仅保存</el-button>
                <el-button type="primary" @click="dialogEditVisible = false" size="small">提交审批</el-button>
            </div>
        </el-dialog>
        <!-- 转给他人模态框 -->
        <el-dialog title="转给他人" width="600px" class="dialogTransfer" :visible.sync="dialogTransferVisible" :close-on-click-modal='false'>
            <el-form ref="form" :model="formTransfer" label-width="120px" size="small">
                <el-form-item label="转移对象：">
                    机会名称
                </el-form-item>
                <el-form-item label="新拥有者：">
                    <el-select v-model="formTransfer.origin">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogTransferVisible = false" size="small">取消</el-button>
                <el-button type="primary" @click="dialogTransferVisible = false" size="small">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                activeName: 'first',            //  tab页签
                tableData: [{date: '2018-10-02', address: 2000}],                  //  表格
                formBasicInfo: {},              //  基本信息
                formAddReceipt: {},              //  新增收款表单
                formAddInvoicing: {},              //  新增开票记录表单
                formEdit: {},                   //  编辑合同表单
                formTransfer: {},               //  转给他人表单
                formDeploy: {},                 //  配置收款计划表单
                dialogReceiptVisible: false,       // 新增收款
                dialogInvoicingVisible: false,       // 新增开票记录
                dialogEditVisible: false,       // 编辑
                dialogTransferVisible: false,   // 转给他人
                dialogDeployVisible: false,   // 配置收款计划
            }
        },
        methods: {
            /**
             * 添加按钮
             */
            handleCommand(command){
                this[command] = true
            },
        }
    }
</script>
<style lang="less">
    @import url('../style/marketing_contract_detail.less');
</style>



